	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Where I Won't Be","date":"17 Jun 2004","slug":"/2004-06-17-where-i-wont-be/","tags":"Y2004"},"html":"<p>I won't be able to make it to <a href=\"http://blogtalk.net/\" title=\"BlogTalk 2.0 - nice to meet you there\">BlogTalk</a> in Vienna next month. I have to do the child care thing at home, as my wife has to be in Lisbon on the 4th. Her company is sponsoring a football tournament there or something.</p>"},"next":{"fields":{"title":"Expert Opinion","date":"17 Jun 2004","slug":"/2004-06-17-expert-opinion/"}},"previous":{"fields":{"title":"DSLed","date":"18 Jun 2004","slug":"/2004-06-18-dsled/"}}},{"node":{"fields":{"title":"Expert Opinion","date":"17 Jun 2004","slug":"/2004-06-17-expert-opinion/","tags":"Y2004"},"html":"<p>We had our appointment today with the speech expert from Hannover. She approved Christopher the <a href=\"http://www.papascott.de/archives/2004/05/19/kindergarten-watch/\">speech therapy kindergarten</a> in Winsen, which was what we wanted. The costs will be split between the county and our health insurance. Now that the plans are concrete, I can make arrangements to work part-time starting in August and maybe do some free-lancing with the extra time I'll have at home. I'm happy that I won't be forced to leave work. </p>\n<p>While Christopher is making improvements in areas like vocabulary, his fluency of speech <em>(Redefluss)</em> is still jerky, sometimes lapsing into stuttering.  It will be good for him to be in a small group of his peers. He won't be starting until fall, but the kindergarten has already invited us to their summer party this weekend. They are hoping for warm weather for water games.</p>"},"next":{"fields":{"title":"PS: Mena Loves You","date":"16 Jun 2004","slug":"/2004-06-16-ps-mena-loves-you/"}},"previous":{"fields":{"title":"Where I Won't Be","date":"17 Jun 2004","slug":"/2004-06-17-where-i-wont-be/"}}},{"node":{"fields":{"title":"PS: Mena Loves You","date":"16 Jun 2004","slug":"/2004-06-16-ps-mena-loves-you/","tags":"Y2004"},"html":"<p><a href=\"http://www.sixapart.com/log/2004/06/announcing_pric.shtml\" title=\"Six Log: Announcing Pricing & Licensing Changes to Movable Type\">New lower pricing</a> for Movable Type, and fairer licenses. Especially cool is the free license for small non-profits.</p>"},"next":{"fields":{"title":"Egil Aarvik","date":"16 Jun 2004","slug":"/2004-06-16-egil-aarvik/"}},"previous":{"fields":{"title":"Expert Opinion","date":"17 Jun 2004","slug":"/2004-06-17-expert-opinion/"}}}],"pathPrefix":"","first":false,"last":false,"index":464,"pageCount":1101}}

